import { Injectable } from '@angular/core';
import { Router, Resolve, ActivatedRouteSnapshot } from '@angular/router';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

import {
  CautionDetailsFragment,
  GetCautionDetailsGQL,
  LanguageCode,
} from '../../../graphql/graphql';

@Injectable({
  providedIn: 'root',
})
export class CautionResolver
  implements Resolve<CautionDetailsFragment | undefined | null> {
  constructor(
    private getCautionDetailsGQL: GetCautionDetailsGQL,
    private router: Router,
  ) {}

  resolve(
    route: ActivatedRouteSnapshot,
  ):
    | CautionDetailsFragment
    | Observable<CautionDetailsFragment | undefined>
    | Promise<CautionDetailsFragment | undefined>
    | null
    | undefined {
    const id = parseInt(route.paramMap.get('id') || '', 10);
    if (id) {
      return this.getCautionDetailsGQL
        .fetch(
          {
            where: {
              id,
            },
          },
          {
            fetchPolicy: 'network-only',
          },
        )
        .pipe(
          map((result) => {
            if (result.error || result.errors) {
              this.router.navigateByUrl('/exception/500');
              return undefined;
            }

            if (result.data?.caution) {
              return result.data.caution;
            } else {
              this.router.navigateByUrl('/exception/404');
              return undefined;
            }
          }),
        );
    } else {
      return {
        translations: [
          {
            language: LanguageCode.Vi,
          },
        ],
      } as CautionDetailsFragment;
    }
  }
}
