import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { CautionDetailsComponent } from './pages/caution-details/caution-details.component';
import { CautionListComponent } from './pages/caution-list/caution-list.component';
import { CautionResolver } from './resolvers/caution.resolver';

const routes: Routes = [
  {
    path: 'new',
    resolve: {
      caution: CautionResolver,
    },
    component: CautionDetailsComponent,
  },
  {
    path: ':id',
    resolve: {
      caution: CautionResolver,
    },
    component: CautionDetailsComponent,
  },
  {
    path: '',
    pathMatch: 'full',
    component: CautionListComponent,
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class CautionRoutingModule {}
