import { Component, OnDestroy, OnInit } from '@angular/core';
import { FormArray, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Data } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { NzMessageService } from 'ng-zorro-antd/message';
import { of, Subject } from 'rxjs';
import { map, mergeMap, takeUntil } from 'rxjs/operators';
import _ from 'lodash';
import { Location } from '@angular/common';

import {
  CautionCreateInput,
  CautionDetailsFragment,
  CautionUpdateInput,
  CreateCautionGQL,
  LanguageCode,
  UpdateCautionGQL,
} from '../../../../graphql/graphql';
import { LanguageService } from '../../../shared/services/language/language.service';

@Component({
  selector: 'app-caution-details',
  templateUrl: './caution-details.component.html',
  styleUrls: ['./caution-details.component.scss'],
})
export class CautionDetailsComponent implements OnInit, OnDestroy {
  LanguageCode = LanguageCode;
  destroy$: Subject<boolean> = new Subject();
  validateForm: FormGroup;
  caution?: CautionDetailsFragment;
  loading = false;

  constructor(
    private fb: FormBuilder,
    private route: ActivatedRoute,
    private location: Location,
    private createCautionGQL: CreateCautionGQL,
    private updateCautionGQL: UpdateCautionGQL,
    private msgService: NzMessageService,
    private translateService: TranslateService,
    private languageService: LanguageService,
  ) {
    this.validateForm = this.fb.group({
      translations: this.fb.array([], Validators.required),
    });
  }

  ngOnInit(): void {
    this.route.data
      .pipe(
        takeUntil(this.destroy$),
        map<Data, CautionDetailsFragment>((value) => value.caution),
      )
      .subscribe((caution) => {
        this.caution = caution;
        this.validateForm.setControl(
          'translations',
          this.fb.array(
            this.languageService
              .sort([...(this.caution?.translations || [])])
              .map((translation) => {
                return this.fb.group({
                  id: [translation.id],
                  name: [translation.name, Validators.required],
                  language: [translation.language, Validators.required],
                  description: [translation.description],
                });
              }) || [],
            Validators.required,
          ),
        );
      });
  }

  get translations(): FormArray {
    return this.validateForm.get('translations') as FormArray;
  }

  addTab(): void {
    const languages = new Set([
      LanguageCode.Vi,
      LanguageCode.En,
      LanguageCode.Ko,
    ]);
    this.translations.controls.forEach((control) =>
      languages.delete(control.value.language),
    );
    const language =
      languages.size === 0 ? LanguageCode.Vi : languages.values().next().value;

    this.translations.push(
      this.fb.group({
        id: [],
        name: [null, Validators.required],
        language: [language, Validators.required],
        description: [],
      }),
    );
  }

  removeTab(index: number): void {
    this.translations.removeAt(index);
  }

  submit(): void {
    Object.values(this.validateForm.controls).forEach((control) => {
      control.markAsDirty();
      control.updateValueAndValidity();
    });
    if (this.validateForm.invalid) {
      return;
    }

    this.loading = true;

    of(this.caution?.id)
      .pipe(
        takeUntil(this.destroy$),
        mergeMap((id) => {
          if (id) {
            const data: CautionUpdateInput = {
              translations: {
                create: this.translations.value
                  ?.filter((translation: any) => !translation.id)
                  .map((translation: any) => _.omit(translation, ['id'])),
                set: this.translations.value?.filter(
                  (translation: any) => translation.id,
                ),
              },
            };
            return this.updateCautionGQL.mutate({
              where: {
                id,
              },
              data,
            });
          } else {
            const data: CautionCreateInput = {
              translations: {
                create: this.translations.value?.map((translation: any) =>
                  _.omit(translation, ['id']),
                ),
              },
            };
            return this.createCautionGQL.mutate({
              data,
            });
          }
        }),
      )
      .subscribe(
        ({ errors, data }) => {
          if (errors) {
            this.msgService.error(
              this.translateService.instant(
                `caution.${this.caution?.id ? 'update' : 'create'}_error`,
              ),
            );
          }

          if (data) {
            this.msgService.success(
              this.translateService.instant(
                `caution.${this.caution?.id ? 'update' : 'create'}_success`,
              ),
            );
            this.location.back();
          }
          this.loading = false;
        },
        () => {
          this.msgService.error(
            this.translateService.instant(
              `caution.${this.caution?.id ? 'update' : 'create'}_error`,
            ),
          );
          this.loading = false;
        },
      );
  }

  cancel(): void {
    this.location.back();
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.unsubscribe();
  }
}
